*,*::after, *::before {
	@include borderbox; }

.main {
	width: 100%;
	height: 118px;
	background: $black;
	margin-bottom: 100px;
	position: relative;
	@include fortablet {
		height: auto;
		margin-bottom: 30px; }
	@include formobile {
		height: auto;
		margin-bottom: 0; }


	&.fixed {
		position: fixed;
		left: 0;
		top: 0;
		z-index: 10; }

	.topbar {
		width: 100%;
		height: 28px;
		background: $red-light-1;

		@include formobile {
			overflow: hidden;
			height: auto;
			max-height: 28px;
			@include transitionspeed(50ms);

			&.slideDown {
				max-height: 1000px;
				@include transitionspeed(1s); } } } }




